  display: flex;
  justify-content: space-between;
  padding: 10px 40px;
  background-color: #f5f5f5;
  border-bottom: 1px solid #ddd;
}
header .logo img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
}
